<template>
  <div class="row">
    <div class="col-md-12">
      <alert 
        :alert="alert"
      />
      <div class="col-md-6 col-sm-6">
        <router-link 
          v-if="categories.length !== 0"
          class="btn btn-primary" 
          to="/categories/add"
        >
          Add new category
        </router-link>
      </div>
      <div class="clearfix"></div>
    </div>
   <div class="col-md-12">
      <div class="card" v-if="categories.length !== 0">
        <paper-table 
          :title="table.title" 
          :sub-title="table.subTitle" 
          :data="table.data" 
          :columns="table.columns"
          :actions="table.actions"
          @clickDelete="handleDeleteCategory"
        />
        <button
          class="btn btn-primary pull-right newUserButton"
          @click="showMoreCategories"
          v-if="isMoreAvialiable || count > 10"
        >
          Show more
        </button>
      </div>
      <jumbotron 
        title="Ups nothing"
        text="Please add more categories"
        v-if="categories.length === 0"
      >
        <router-link 
          class="btn btn-primary" 
          to="/categories/add"
        >
          Add new Category
        </router-link>
      </jumbotron>
    </div>
  </div>
</template> 

<script>
  import { mapGetters, mapActions, mapState } from 'vuex'
  import { tableColumns, tableActions } from './TableMock.js'

  export default {
    components: {
      PaperTable: () => import('components/UIComponents/PaperTable.vue')
    },
    data: () => ({
      tableActions,
      tableColumns,
      table: {
        title: 'Categories list',
        subTitle: 'Categoires existing list',
        columns: [...tableColumns],
        actions: [...tableActions],
        data: []
      }
    }),
    mounted() {
      this.fetchCategories()
    },
    watch: {
      categories(value) {
        if (value) {
          this.table.data = [
            ...value
          ]
        }
      }
    },
    computed: {
      ...mapGetters({
        categories: 'getCategories',
        isMoreAvialiable: 'isMoreAvialiable'
      }),
      ...mapState({
        alert: state => state.alert,
        user: state => state.user,
        count: ({ categories: { pagination } }) => pagination.count
      })
    },
    methods: {
      handleDeleteCategory(value) {
        const id = value
        this.removeCategory({ id })
      },
      ...mapActions([
        'fetchCategories',
        'removeCategory',
        'showMoreCategories'
      ])
    }
  }
</script>

<style lang="css" scoped>
  .newUserButton {
    margin: 10px 0px;
    display: block;
    width: 100%;
  }

  .alert {
    margin: 0px 0px 10px;
  }
</style> 



